<template lang="html">
  <div>
    <h2></h2>
    <pzcheckbox name="c1" value="1" :checked="true"></pzcheckbox>
    <pzcheckbox name="c1" value="2">sdf</pzcheckbox>
    <pzcheckbox name="c1" value="3">sdf</pzcheckbox>
    <pzcheckbox name="c1" value="4" :disabled="true"></pzcheckbox>
    <h2>change</h2>
    <pzcheckbox name="c1" value="" :change="change">change</pzcheckbox>
    <h2></h2>
    <pzcheckbox name="c1" value="5" :checked="checked" :change="change1"></pzcheckbox>
    <br>
    <br>
    <pzbutton @click.native="changeChecked"></pzbutton>

  </div>
</template>

<script>
import pzbutton from '../components/button/index';
import pzcheckbox from '../components/checkbox/index';
// import pzcheckbox from 'pzvue-checkbox';

export default {
  data(){
    return {
      checked: true
    }
  },
    components: {
        pzbutton,
        pzcheckbox,
    },
    methods:{
      change: function(value, ischecked) {
        console.log("=" +value);
        console.log("=" + ischecked);
      },
      change1: function(value, ischecked) {
        console.log("=" +value);
        console.log("=" + ischecked);
      },
      changeChecked: function() {
        this.checked = !this.checked;
      },
    }
}
</script>

<style lang="css">
</style>



