<template lang="html">
  <div>
    <h2></h2>
    <pz-form ref="form">
      <pz-formitem label="" :validate="rules.name">
        <pzinput v-model="form.name" placeholder=""></pzinput>
      </pz-formitem>
      <pz-formitem label="" :validate="rules.area">
        <pzinput v-model="form.area" placeholder=""></pzinput>
      </pz-formitem>
      <pz-formitem label="" :validate="rules.time">
        <pzinput v-model="form.time"></pzinput>
      </pz-formitem>
      <pz-formitem label="" :validate="rules.ps">
        <pzinput v-model="form.ps"></pzinput>
      </pz-formitem>
      <pz-formitem label="" :validate="rules.xz">
        <pz-checkboxgroup v-model="form.xz">
          <pzcheckbox name="form1" value="1" checked="true"></pzcheckbox>
          <pzcheckbox name="form1" value="2" checked="true"></pzcheckbox>
          <pzcheckbox name="form1" value="3" checked="true"></pzcheckbox>
          <pzcheckbox name="form1" value="4" checked="true"></pzcheckbox>
        </pz-checkboxgroup>
      </pz-formitem>
      <pz-formitem label="">
        <pz-radiogroup v-model="form.zy">
          <pzradio name="formradio" value="1" checked="true" v-model="form.time"></pzradio>
          <pzradio name="formradio" value="2"></pzradio>
        </pz-radiogroup>
      </pz-formitem>
      <pz-formitem label="" :validate="rules.xs">
        <pzinput v-model="form.xs"></pzinput>
      </pz-formitem>
      <pz-formitem><pzbutton  @click.native="submitHandle"></pzbutton></pz-formitem>
    </pz-form>
    <br><br>
    result11d:  {{form}}
  </div>
</template>

<script>
import validate from '../tools/validate.js';
import pzbutton from '../components/button/index';
import pzcheckbox from '../components/checkbox/index';
import pzinput from '../components/input/index';
import pzinputnumber from '../components/input-number/index';
import pzradio from '../components/radio/index';
import pzform from '../components/form/index';
import pzformitem from '../components/form-item/index';
import pzcheckboxgroup from '../components/checkbox-group/index';
import pzradiogroup from '../components/radio-group/index';

export default {
  data(){
    return {
      form:{
        name:'',
        area:"",
        time:"",
        xz:[],
        zy:"",
        ps:"",
        xs:""
      },
      rules: {
        name:{min:1,max:20,message:"1-20"},
        area:{ min:1,max:30,message:"1-20"},
        time:{required: false, reg:'time',message:"1-20,"},
        xz:{type:'array', min:3,max:4,message:""},
        ps:{ min:1,max:20,message:"1-20"},
        xs:{ min:1,max:20,message:"1-20"},
      }
    }
  },
    components: {
        pzbutton,
        pzcheckbox,
        pzinput,
        pzinputnumber,
        pzradio,
        "pz-form":pzform,
        "pz-formitem":pzformitem,
        "pz-checkboxgroup":pzcheckboxgroup,
        "pz-radiogroup":pzradiogroup,
    },
    methods:{
      submitHandle: async function() {
        let ischeck =  await this.$refs.form.validate();
        if(ischeck) {//
          //TODO: submit or ajax
        } else{
          console.log("");
          let id = this.$layer.alert("this is demo", {
            title: ""
          },function() {
            this.$layer.close(id);
          });
        }
      }
    }
}
</script>

<style lang="css">
</style>



