<template lang="html">
  <div>
    <h2></h2>
    <pzbutton></pzbutton>
    <pzbutton btn="success"></pzbutton>
    <pzbutton btn="info"></pzbutton>
    <pzbutton btn="warning"></pzbutton>
    <pzbutton btn="danger"></pzbutton>
    <pzbutton btn="danger" :disabled="true"></pzbutton>
    <h2></h2>
    <pzbutton icon="user-md"></pzbutton>
    <pzbutton icon="user-md" btn="success"></pzbutton>
    <pzbutton icon="user-md" btn="info"></pzbutton>
    <pzbutton btn="danger" :loading="true"></pzbutton>
    <h2></h2>
    <pzbutton icon="user-md" size="block"></pzbutton>
    <pzbutton icon="user-md" btn="success" size="block"></pzbutton>
    <pzbutton icon="user-md" btn="info" size="block"></pzbutton>
    <pzbutton btn="danger" :loading="true" size="block"></pzbutton>
  </div>
</template>

<script>
import validate from '../tools/validate.js';
import pzbutton from 'pzvue-button';
import pzcheckbox from '../components/checkbox/index';
import pzinput from '../components/input/index';
import pzinputnumber from '../components/input-number/index';
import pzradio from '../components/radio/index';
import pzform from '../components/form/index';
import pzformitem from '../components/form-item/index';
import pzcheckboxgroup from '../components/checkbox-group/index';
import pzradiogroup from '../components/radio-group/index';

export default {
  data(){
    return {
      form:{
        name:'',
        area:"",
        time:"",
        xz:[],
        zy:"",
        ps:"",
        xs:""
      },
      rules: {
        name:{min:1,max:20,message:"1-20"},
        area:{ min:1,max:30,message:"1-20"},
        time:{required: false, reg:'time',message:"1-20,"},
        xz:{type:'array', min:3,max:4,message:""},
        ps:{ min:1,max:20,message:"1-20"},
        xs:{ min:1,max:20,message:"1-20"},
      }
    }
  },
    components: {
        pzbutton,
        pzcheckbox,
        pzinput,
        pzinputnumber,
        pzradio,
        "pz-form":pzform,
        "pz-formitem":pzformitem,
        "pz-checkboxgroup":pzcheckboxgroup,
        "pz-radiogroup":pzradiogroup,
    },
    methods:{
      submitHandle: async function() {
        let ischeck =  await this.$refs.form.validate();
        if(ischeck) {//
          //TODO: submit or ajax
        } else{
          console.log("");
        }
      }
    }
}
</script>

<style lang="css">
</style>



