<template lang="html">
    <div>
        <h2></h2>
        <pz-select :options="options" v-model="form.name" placeholder=""></pz-select>
        <br>
        <br> value={{form.name}}

        <h2></h2>
        <pz-select :options="options1" v-model="form.select1"></pz-select>
        <br>
        <br> value={{form.select1}}
        <h2>change</h2>
        <pz-select :options="options1" :change="change" :default="3"></pz-select>
        <br><br>
        <p id="change"></p>
        <h2>change</h2>
        <pz-select :options="options1" :change="change1" :default="defvalue"></pz-select>
        <br><br>
        <pz-button @click.native="changeValue">value()</pz-button>
        <p id="change1"></p>
    </div>
</template>

<script>
    import pzselect from 'pzvue-select';
    import pzbutton from 'pzvue-button';

    export default {
        data() {
            return {
              defvalue: 3,
                form: {
                    name: '',
                    select1: ''
                },
                options: [{
                    text: "",
                    value: -1
                }, {
                    text: "",
                    value: 0
                }, {
                    text: "",
                    value: 1
                }, {
                    text: "",
                    value: 2
                }, {
                    text: "",
                    value: 3,
                    default: true
                }, {
                    text: "",
                    value: 4
                }],
                options1: [{
                    text: '<i class="icon-star"></i>',
                    value: 0
                }, {
                    text: '<i class="icon-star"></i><i class="icon-star"></i>',
                    value: 1
                }, {
                    text: '<i class="icon-star"></i><i class="icon-star"></i><i class="icon-star"></i>',
                    value: 2
                }, {
                    text: '<i class="icon-star"></i><i class="icon-star"></i><i class="icon-star"></i><i class="icon-star"></i>',
                    value: 3
                }, {
                    text: '<i class="icon-star"></i><i class="icon-star"></i><i class="icon-star"></i><i class="icon-star"></i><i class="icon-star"></i>',
                    value: 4,
                    default: true
                }]
            }
        },
        components: {
            "pz-select": pzselect,
            "pz-button": pzbutton,
        },
        methods: {
          change: function(val) {
            document.getElementById("change").innerHTML = '' + val;
            console.log("");
          },
          change1: function(val) {
            document.getElementById("change1").innerHTML = '' + val;
            console.log("");
          },
          changeValue: function() {
            console.log("456sd");
            this.defvalue = (Date.parse(new Date()) / 1000) % 4;
          }
        }
    }
</script>

<style lang="css">

</style>



